.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 10px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.edit-grid {
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding-bottom: 12px;
}

.edit-grid-textarea {
  width: 230px;
  border-radius: 3px;
  background-color: white;
  border-color: #696969;
}

.edit-grid-action {
  cursor: pointer;
  border-radius: 3px;
  color: #19bc8b;
  background-color: white;
  border-color: #696969;
  margin: 0 12px;
  padding: 2rem;
  font-size: 4rem;
}

.edit-grid-action:disabled {
  cursor: not-allowed;
  color: #696969;
}

.edit-grid-action:focus {
  outline: none;
}

button:not([disabled]).raise:hover {
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  -webkit-box-shadow: 0 0 0.3em .02em #19bc8b;
          box-shadow: 0 0 0.3em .02em #19bc8b;
  -webkit-transform: translateY(-0.1em);
      -ms-transform: translateY(-0.1em);
          transform: translateY(-0.1em);
}

.width-height {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

#game-canvas {
    display: inline-block;
    width: 1000px;
    height: 400px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



